<template>
<el-footer class="time">
   {{birthTimeNow}}
   {{meetTimeNow}}
   {{loveTimeNow}}
</el-footer>
</template>

<script>
import moment from 'moment';
let nowTime=moment().format("YYYY-MM-DD HH:mm:ss")
let birthTimeNow=moment(nowTime).diff(moment("2001-10-14 00:00:00"), "seconds")
let meetTimeNow=moment(nowTime).diff(moment("2017-09-01 00:00:00"), "seconds")
let loveTimeNow=moment(nowTime).diff(moment('2018-01-01 00:00:00'), "seconds")
export default {
   name:'Footer',
   data() {
      return {
        nowTime,
        meetTimeNow,
        loveTimeNow,
        birthTimeNow,
      };
    }, 
   mounted(){
    let timer= setInterval(() => {
       this.birthTimeNow++
       this.meetTimeNow++
       this.loveTimeNow++
      }, 1000);
   }
}
</script>

<style>
.time {
    width: 100%;
    margin-top: 50px;
    background-color: blue;
    color: red;
}
</style>