import Vue from "vue"
import VueRouter from "vue-router";
Vue.use(VueRouter)
import Timeline from "@/pages/Timeline"
import Home from "@/pages/Home"
import Time from "@/pages/Time"
export default new VueRouter({
    routes:[
        {
          path:'/home',
          component:Home
        },
        {
            path:'/timeline',
            component:Timeline
        },
        {
            path:'/time',
            component:Time
        },
        {
            path:'*',
            redirect:"Home",
        }
        
    ]
})